export default "@import url(\"https://fonts.googleapis.com/css?family=DM+Sans:400,500|Jost:400,500,600&display=swap\");\n* {\n  box-sizing: border-box;\n}\nbody {\n  color: #2b2c48;\n  font-family: \"Jost\", sans-serif;\n  background-image: url(https://images.unsplash.com/photo-1566738780863-f9608f88f3a9?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2378&q=80);\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-position: center;\n  background-attachment: fixed;\n  min-height: 100vh;\n  display: flex;\n  flex-wrap: wrap;\n  padding: 20px;\n}\n.card {\n  max-width: 340px;\n  margin: auto;\n  overflow-y: auto;\n  position: relative;\n  z-index: 1;\n  overflow-x: hidden;\n  background-color: white;\n  display: flex;\n  transition: 0.3s;\n  flex-direction: column;\n  border-radius: 10px;\n  box-shadow: 0 0 0 8px rgba(255, 255, 255, 0.2);\n}\n.card[data-state=\"#about\"] {\n  height: 450px;\n}\n.card[data-state=\"#about\"] .card-main {\n  padding-top: 0;\n}\n.card[data-state=\"#contact\"] {\n  height: 430px;\n}\n.card[data-state=\"#experience\"] {\n  height: 550px;\n}\n.card.is-active .card-header {\n  height: 80px;\n}\n.card.is-active .card-cover {\n  height: 100px;\n  top: -50px;\n}\n.card.is-active .card-avatar {\n  transform: none;\n  left: 20px;\n  width: 50px;\n  height: 50px;\n  bottom: 10px;\n}\n.card.is-active .card-fullname,\n.card.is-active .card-jobtitle {\n  left: 86px;\n  transform: none;\n}\n.card.is-active .card-fullname {\n  bottom: 18px;\n  font-size: 19px;\n}\n.card.is-active .card-jobtitle {\n  letter-spacing: 1px;\n  font-size: 10px;\n  padding-top: 1em;\n}\n.card-header {\n  position: relative;\n  display: flex;\n  height: 200px;\n  flex-shrink: 0;\n  width: 100%;\n  transition: 0.3s;\n}\n.card-header * {\n  transition: 0.3s;\n}\n.card-cover {\n  width: 100%;\n  height: 100%;\n  position: absolute;\n  height: 160px;\n  top: -20%;\n  left: 0;\n  will-change: top;\n  background-size: cover;\n  background-position: center;\n  filter: blur(30px);\n  transform: scale(1.2);\n  transition: 0.5s;\n}\n.card-avatar {\n  width: 100px;\n  height: 100px;\n  box-shadow: 0 8px 8px rgba(0, 0, 0, 0.2);\n  border-radius: 50%;\n  -o-object-position: center;\n     object-position: center;\n  -o-object-fit: cover;\n     object-fit: cover;\n  position: absolute;\n  bottom: 0;\n  left: 50%;\n  transform: translateX(-50%) translateY(-64px);\n}\n.card-fullname {\n  position: absolute;\n  bottom: 0;\n  font-size: 22px;\n  font-weight: 700;\n  text-align: center;\n  white-space: nowrap;\n  transform: translateY(-15px) translateX(-50%);\n  left: 50%;\n}\n.card-jobtitle {\n  position: absolute;\n  bottom: 0;\n  font-size: 11px;\n  white-space: nowrap;\n  font-weight: 500;\n  opacity: 0.7;\n  text-transform: uppercase;\n  letter-spacing: 1.5px;\n  margin: 0;\n  left: 50%;\n  transform: translateX(-50%) translateY(-2px);\n  padding-top: 1em;\n}\n.card-main {\n  position: relative;\n  flex: 1;\n  display: flex;\n  padding-top: 10px;\n  flex-direction: column;\n}\n.card-subtitle {\n  font-weight: 700;\n  font-size: 13px;\n  margin-bottom: 8px;\n}\n.card-content {\n  padding: 20px;\n}\n.card-desc {\n  line-height: 1.6;\n  color: #636b6f;\n  font-size: 14px;\n  margin: 0;\n  font-weight: 400;\n  font-family: \"DM Sans\", sans-serif;\n}\n.card-social {\n  display: flex;\n  align-items: center;\n  padding: 0 20px;\n  margin-bottom: 30px;\n}\n.card-social svg {\n  fill: #a5b5ce;\n  width: 16px;\n  display: block;\n  transition: 0.3s;\n}\n.card-social a {\n  color: #8797a1;\n  height: 32px;\n  width: 32px;\n  border-radius: 50%;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  transition: 0.3s;\n  background-color: rgba(93, 133, 193, 0.05);\n  border-radius: 50%;\n  margin-right: 10px;\n}\n.card-social a:hover svg {\n  fill: #637faa;\n}\n.card-social a:last-child {\n  margin-right: 0;\n}\n.card-buttons {\n  display: flex;\n  background-color: #fff;\n  margin-top: auto;\n  position: -webkit-sticky;\n  position: sticky;\n  bottom: 0;\n  left: 0;\n}\n.card-buttons button {\n  flex: 1 1 auto;\n  -webkit-user-select: none;\n     -moz-user-select: none;\n      -ms-user-select: none;\n          user-select: none;\n  background: 0;\n  font-size: 13px;\n  border: 0;\n  padding: 15px 5px;\n  cursor: pointer;\n  color: #5c5c6d;\n  transition: 0.3s;\n  font-family: \"Jost\", sans-serif;\n  font-weight: 500;\n  outline: 0;\n  border-bottom: 3px solid transparent;\n}\n.card-buttons button.is-active, .card-buttons button:hover {\n  color: #2b2c48;\n  border-bottom: 3px solid #8a84ff;\n  background: linear-gradient(to bottom, rgba(127, 199, 231, 0) 0%, rgba(207, 204, 255, 0.2) 44%, rgba(211, 226, 255, 0.4) 100%);\n}\n.card-section {\n  display: none;\n}\n.card-section.is-active {\n  display: block;\n  -webkit-animation: fadeIn 0.6s both;\n          animation: fadeIn 0.6s both;\n}\n@-webkit-keyframes fadeIn {\n  0% {\n    opacity: 0;\n    transform: translatey(40px);\n  }\n  100% {\n    opacity: 1;\n  }\n}\n@keyframes fadeIn {\n  0% {\n    opacity: 0;\n    transform: translatey(40px);\n  }\n  100% {\n    opacity: 1;\n  }\n}\n.card-timeline {\n  margin-top: 30px;\n  position: relative;\n}\n.card-timeline:after {\n  background: linear-gradient(to top, rgba(134, 214, 243, 0) 0%, #516acc 100%);\n  content: \"\";\n  left: 42px;\n  width: 2px;\n  top: 0;\n  height: 100%;\n  position: absolute;\n  content: \"\";\n}\n.card-item {\n  position: relative;\n  padding-left: 60px;\n  padding-right: 20px;\n  padding-bottom: 30px;\n  z-index: 1;\n}\n.card-item:last-child {\n  padding-bottom: 5px;\n}\n.card-item:after {\n  content: attr(data-year);\n  width: 10px;\n  position: absolute;\n  top: 0;\n  left: 37px;\n  width: 8px;\n  height: 8px;\n  line-height: 0.6;\n  border: 2px solid #fff;\n  font-size: 11px;\n  text-indent: -35px;\n  border-radius: 50%;\n  color: rgba(134, 134, 134, 0.7);\n  background: linear-gradient(to bottom, #a0aee3 0%, #516acc 100%);\n}\n.card-item-title {\n  font-weight: 500;\n  font-size: 14px;\n  margin-bottom: 5px;\n}\n.card-item-desc {\n  font-size: 13px;\n  color: #6f6f7b;\n  line-height: 1.5;\n  font-family: \"DM Sans\", sans-serif;\n}\n.card-contact-wrapper {\n  margin-top: 20px;\n}\n.card-contact {\n  display: flex;\n  align-items: center;\n  font-size: 13px;\n  color: #6f6f7b;\n  font-family: \"DM Sans\", sans-serif;\n  line-height: 1.6;\n  cursor: pointer;\n}\n.card-contact + .card-contact {\n  margin-top: 16px;\n}\n.card-contact svg {\n  flex-shrink: 0;\n  width: 30px;\n  min-height: 34px;\n  margin-right: 12px;\n  transition: 0.3s;\n  padding-right: 12px;\n  border-right: 1px solid #dfe2ec;\n}\n.contact-me {\n  border: 0;\n  outline: none;\n  background: linear-gradient(to right, rgba(83, 200, 239, 0.8) 0%, rgba(81, 106, 204, 0.8) 96%);\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.15);\n  color: #fff;\n  padding: 12px 16px;\n  width: 100%;\n  border-radius: 5px;\n  margin-top: 25px;\n  cursor: pointer;\n  font-size: 14px;\n  font-weight: 500;\n  font-family: \"Jost\", sans-serif;\n  transition: 0.3s;\n}\n/*# sourceMappingURL=data:application/json;base64, */";